<script>
  import { onMount } from "svelte";

  onMount(() => {
    const numSort = (a, b) => (a < b ? 1 : -1);

    const canvas = document.getElementById("canvas");
    const ctx = canvas.getContext("2d");

    let windowWidth = window.innerWidth;
    let windowHeight = window.innerHeight;

    const lineStarts = [];

    canvas.width = windowWidth;
    canvas.height = windowHeight;

    ctx.fillStyle = "#333";
    ctx.fillRect(0, 0, windowWidth, windowHeight);
    drawLines(100, 20, true);

    function lineFromTo(startPoint, endPoint) {
      const xSegments = [];
      const ySegments = [];
      const numSegments = Math.max(3, Math.ceil(Math.random() * 8));

      for (let i = 0; i < numSegments; i++) {
        xSegments[i] = Math.floor(
          Math.sin(Math.PI * 2) * (endPoint.x - startPoint.x) + startPoint.x
        );
        ySegments[i] = Math.floor(
          Math.cos(Math.PI * 2) * (endPoint.y - startPoint.y) + startPoint.y
        );
      }

      return {
        xSegments: xSegments,
        ySegments: ySegments.sort(numSort)
      };
    }

    function drawLines(numLines, numLayers, resetLines) {
      let startPoint;
      let layersToDraw = numLayers;
      let linesToDraw = numLines;

      if (resetLines) {
        for (let i = 0; i < numLayers; i++) {
          lineStarts[i] = [];
          for (let j = 0; j < numLines; j++) {
            lineStarts[i][j] = {
              x: windowWidth * Math.random(),
              height:
                windowHeight +
                Math.random() * -(windowHeight / 1.5) * (i / numLayers)
            };
          }
        }
      }

      while (layersToDraw > 0) {
        while (linesToDraw > 0) {
          const { xSegments, ySegments } = lineFromTo(
            {
              x: lineStarts[layersToDraw - 1][linesToDraw - 1].x,
              y: windowHeight
            },
            {
              x:
                lineStarts[layersToDraw - 1][linesToDraw - 1].x +
                Math.random() * 30 -
                15,
              y: lineStarts[layersToDraw - 1][linesToDraw - 1].height
            }
          );
          ctx.lineWidth = layersToDraw * 10;
          ctx.beginPath();
          ctx.moveTo(
            lineStarts[layersToDraw - 1][linesToDraw - 1].x,
            windowHeight
          );
          xSegments.forEach((segment, i) => {
            ctx.strokeStyle = ctx.fillStyle = `hsla(${360 *
              (segment / windowWidth)}, ${layersToDraw * 1.5}%, 50%, ${(1 /
              layersToDraw) *
              0.4})`;
            ctx.lineTo(segment, ySegments[i]);
            ctx.stroke();
            ctx.beginPath();
            ctx.moveTo(segment, ySegments[i]);
          });
          linesToDraw--;
        }
        layersToDraw--;
        linesToDraw = numLines;
      }
    }
  });
</script>

